.novel-sidebar {
  width: 250px;
  background: var(--bg-primary);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  margin-left: 40px;
  transition: all 0.25s cubic-bezier(.4, 1.4, .6, 1);
  transform: translateX(0);
  opacity: 1;
  z-index: 1000;
  height: 100%;
  box-shadow: 2px 0 8px var(--shadow);
}

.novel-sidebar.closed {
  transform: translateX(-100%);
  opacity: 0;
  pointer-events: none;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-secondary);
  color: var(--text-primary);
  transition: all 0.3s ease;

  h3 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
  }
}

.close-panel-btn {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  width: 28px;
  height: 28px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-muted);
  border-radius: 4px;
  transition: all 0.15s;

  &:hover {
    background: var(--bg-tertiary);
    color: var(--text-primary);
  }
}

.sidebar-content {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;
}

.section {
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 8px;
  }
}

.section-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 20px;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-muted);
  border-bottom: 1px solid var(--border-color);
  margin-bottom: 8px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background: var(--bg-tertiary);
    color: var(--text-primary);
  }

  .title-text {
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .expand-arrow {
    font-size: 10px;
    transition: transform 0.3s ease;
    color: var(--text-muted);
    
    &.expanded {
      transform: rotate(90deg);
      color: var(--text-secondary);
    }
  }

  .count {
    color: var(--text-muted);
    font-size: 11px;
  }
}

.items-container {
  padding: 0 12px;
  transition: all 0.3s ease;
  
  &.collapsed {
    height: 0 !important;
    opacity: 0;
    overflow: hidden;
    padding: 0 12px;
  }
}

.sidebar-item {
  display: flex;
  align-items: flex-start;
  padding: 12px 8px;
  margin-bottom: 4px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  border: 1px solid transparent;

  &:hover {
    background: var(--bg-tertiary);
    border-color: var(--border-color);
    transform: translateY(-1px);
    box-shadow: 0 2px 8px var(--shadow);
  }

  &:active {
    transform: translateY(0);
    box-shadow: 0 1px 4px var(--shadow);
  }
}

.item-content {
  flex: 1;
  min-width: 0;
}

.item-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 4px;
  line-height: 1.3;
  transition: color 0.3s ease;
}

.item-description {
  font-size: 12px;
  color: var(--text-secondary);
  line-height: 1.4;
  margin-bottom: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  transition: color 0.3s ease;
}

.item-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  color: var(--text-muted);
  transition: color 0.3s ease;

  .date {
    font-weight: 500;
  }

  .word-count {
    background: var(--bg-accent);
    color: var(--accent-primary);
    padding: 2px 6px;
    border-radius: 10px;
    font-weight: 600;
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  text-align: center;
  color: var(--text-muted);
  transition: color 0.3s ease;

  .empty-icon {
    font-size: 48px;
    margin-bottom: 16px;
    opacity: 0.6;
  }

  p {
    font-size: 13px;
    line-height: 1.5;
    margin: 0;
  }
}

/* 스크롤바 스타일 */
.sidebar-content::-webkit-scrollbar {
  width: 6px;
}

.sidebar-content::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
}

.sidebar-content::-webkit-scrollbar-thumb {
  background: var(--text-muted);
  border-radius: 3px;

  &:hover {
    background: var(--text-secondary);
  }
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .novel-sidebar {
    width: 200px;
  }

  .sidebar-header {
    padding: 12px 16px;

    h3 {
      font-size: 14px;
    }
  }

  .section-title {
    padding: 6px 16px;
    font-size: 12px;
  }

  .items-container {
    padding: 0 8px;
  }

  .sidebar-item {
    padding: 10px 6px;
  }

  .item-title {
    font-size: 13px;
  }

  .item-description {
    font-size: 11px;
  }
}